import fetcher from '../utils/fetcher';
import { useState, useEffect, useCallback } from 'react'
import { useInView } from "react-intersection-observer"
import LeaderBoardContents from './LeaderBoardContents';

export default function LeaderBoard() {
    const [prev, prevInView] = useInView();  //   ref - 
    const [next, nextInView] = useInView();  //   ref - 
    const [page, setPage] = useState(1);
    const [loading, setLoading] = useState(false);

    const [items, setItems] = useState([]);

    const getItems = useCallback( async () => {
        if(page<=1) {setPage(1)}   // page 0  
        setLoading(true);
        console.log(""+page);
        //localStore.selectData(page)
        fetcher.load(page).then((data)=>{
            setItems(data);
            setLoading(false);
        })
      }, [page])

    useEffect(() => {
        getItems()
    }, [getItems])
    
    useEffect(()=>{
        console.log("Check : "+prevInView);
        if ( prevInView) {
            
            console.log("prev");
            setPage(prevState => prevState - 1);
          }
    },[prevInView])

    useEffect(()=>{
        if (nextInView && !loading) {
            console.log("next");
            setPage(prevState => prevState + 1)
          }
    },[nextInView])

    return (
        <ul className="sidebarsection-rank">
            {
                items.map((s, i) => {
                    if(i===3) {
                        return<li key={s._id} ref={prev} style={{"backgroundColor" : "black"}}><LeaderBoardContents s={s}/></li>  
                    }
                    if(i===items.length-1) {
                        return<li key={s._id} ref={next} style={{"backgroundColor" : "black"}}><LeaderBoardContents s={s}/></li>  
                    }
                    return <li key={s._id}><LeaderBoardContents s={s}/></li>
                })
            }
        </ul>
    )
}